    --lazy-color-primary-200: var(--lazy-color-warning-200);
    --lazy-color-primary-300: var(--lazy-color-warning-300);
    --lazy-color-primary-400: var(--lazy-color-warning-400);
    --lazy-color-primary-500: var(--lazy-color-warning-500);
  }
  :root[theme-color=danger] {
    --lazy-color-primary-100: var(--lazy-color-danger-100);
    --lazy-color-primary-200: var(--lazy-color-danger-200);
    --lazy-color-primary-300: var(--lazy-color-danger-300);
    --lazy-color-primary-400: var(--lazy-color-danger-400);
