@layer components {
    ._lazy-tooltip {
        @apply absolute z-50 invisible opacity-0 transition-all duration-300 ease-in-out text-[0.66rem] font-medium bg-gray-900 dark:bg-gray-700 text-white rounded-lg px-2 py-1;
        
        &.is-active {
            @apply md:visible md:opacity-100 invisible opacity-0;
        }
    }
}