@layer components {
    ._lazy-badge-xs {
        @apply text-xs font-bold py-1 px-2 rounded-md;

        &.badge-info {
            @apply bg-info-300/15 text-info-400 dark:text-info-200;
        }
        
        &.badge-success {
            @apply bg-success-300/15 text-success-400 dark:text-success-200;
        }
    }
}
